@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);


*{margin:0;padding:0;box-sizing:border-box}body{max-height:100vh;background-color:#222;font-family:'Open Sans', sans-serif}a,a:link,a:visited,a:hover,a:active{height:100%;width:100%;display:block;color:inherit;text-decoration:none}.navbar{margin-top:100px;position:relative;display:flex;justify-content:center;align-items:flex-start}.navbar .menu{width:600px;position:absolute;border-top:2px solid #4c0;-webkit-border-image:-webkit-gradient(linear, left top, right top, color-stop(25%, #a3ec7e), color-stop(50%, #42eefa), color-stop(75%, #f371ff)) 1;-webkit-border-image:linear-gradient(90deg, #a3ec7e 25%, #42eefa 50%, #f371ff 75%) 1;border-image:-webkit-gradient(linear, left top, right top, color-stop(25%, #a3ec7e), color-stop(50%, #42eefa), color-stop(75%, #f371ff)) 1;border-image:linear-gradient(90deg, #a3ec7e 25%, #42eefa 50%, #f371ff 75%) 1;box-shadow:0px 5px 20px -5px rgba(0,0,0,0.3);display:flex;flex-direction:row;list-style:none}.navbar .menu__item{width:100%;padding:10px 15px;display:flex;justify-content:center;align-items:center;background-color:#333;color:white;-webkit-transition:.2s;transition:.2s}.navbar .menu__item:hover{background:gray;color:#000;cursor:pointer}.submenu{width:240px;-webkit-transform:translateX(180px);transform:translateX(180px);border:1px solid #999;border:1px solid transparent;box-shadow:0px 10px 20px -5px rgba(0,0,0,0.3);border-radius:5px;position:absolute;top:42px;left:0;display:flex;flex-direction:column;justify-content:center;align-content:center;overflow:hidden;-webkit-transition:.3s ease-out;transition:.3s ease-out;background-size:300% 300%;opacity:0;visibility:hidden;-webkit-animation:bgmove 5s ease infinite;animation:bgmove 5s ease infinite;z-index:-1;pointer-events:none}.submenu--active{visibility:visible;opacity:1;z-index:9;pointer-events:auto}.submenu__item{width:100%;padding:10px 15px;background-color:#333;color:white;display:flex;justify-content:center;align-items:center;flex-grow:1;-webkit-transition:.2s;transition:.2s}.submenu__item:not(:last-child){border-bottom:1px solid #1a1a1a}.submenu__item:first-child{border-radius:5px 5px 0 0}.submenu__item:last-child{border-radius:0 0 5px 5px}.submenu__item:hover{background:gray;color:#000;cursor:pointer}@-webkit-keyframes bgmove{0%{background-position:10% 50%}50%{background-position:90% 50%}100%{background-position:10% 50%}}@keyframes bgmove{0%{background-position:10% 50%}50%{background-position:90% 50%}100%{background-position:10% 50%}}

